import React from "react"
import ReactDOM from "react-dom/client"

import { App } from "./app"
import "./index.css"
// import reportWebVitals from "./reportWebVitals"
import { error } from "./utils"

const reactRoot =
    document.getElementById("react") ??
    error("No root for React app -- looking for element with id='#react'.")
const scratchPad =
    document.getElementById("scratch-pad") ??
    error("No scratch <div/> -- looking for element with id='#scratch-pad'.")
const app = ReactDOM.createRoot(reactRoot)

app.render(
    <React.StrictMode>
        <App scratchPad={scratchPad} />
    </React.StrictMode>
)

